@import '../../stylesheets/utils/colors';
@import '../../stylesheets/utils/mixins';
.bg_page {
    width: 100vw;
    height: 100vh;
  
    background-color: var(--background-grey-color);
  
    @include mq(tab) {
      background-image: url('../../images/login/ellipse1.svg'),
        url('../../images/login/ellipse2.svg');
      background-repeat: no-repeat;
      background-position: left bottom, right top;
    }
  }
  
  .bg_blur {
    width: 100vw;
    height: 100vh;
  
    backdrop-filter: blur(25px);
  }
  
  .wrapper {
    width: 100%;
    height: 100vh;
  
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
  }
  
  .img {
    width: 300px;
    height: 150px;
    object-fit: contain;
  
    @include mq(tab) {
      width: 600px;
      height: 300px;
    }
  }
  
  .title {
    font-weight: 700;
    font-size: 24px;
    line-height: 1.5;
    @include mq(tab) {
      font-size: 40px;
    }
  }
  
  .desc {
    font-weight: 400;
    font-size: 18px;
    line-height: 1.5;
    @include mq(tab) {
      font-size: 26px;
    }
  }
  
  .link {
    color: var(--main-accent-color-blue);
    text-decoration: underline;
  }
  